import React, { useEffect, useState } from 'react'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import axios from 'axios'

import AdvertisementApiList from '../../../AdvertisementApiList';
import ApiHeaderAdvrt from '../../../ApiHeaderAdvrt';
import Loader from '../../../Advertisement/Loader';
import BackButton from '../../../Advertisement/BackButton';
import AdvrtSuccessScreen from '../../../Advertisement/AdvrtSuccessScreen';
import ReviewLodge from './ReviewLodge';
import LodgeForm from './LodgeForm';
import LodgeDocForm from './LodgeDocForm';
import { useParams } from 'react-router-dom';


function LodgeIndex() {
    const [formIndex, setFormIndex] = useState(1) //formindex specifies type of form  at index 1 ...
    const [allFormData, setAllFormData] = useState([])
    const [responseScreen, setresponseScreen] = useState()
    const [reviewData, setreviewData] = useState({})
    const [show, setshow] = useState(false)
    const [existingApplicationDetails, setexistingApplicationDetails] = useState()
    const [applicationType, setapplicationType] = useState()
    const [isLoading, setisLoading] = useState(false);
    const [erroState, seterroState] = useState(false);

    const { id, type } = useParams();
    console.log("self advertisement param", id)
    console.log("self advertisement param", type)

    const showLoader = (val) => {
        setshow(val);
    }

    const { api_postLodgeApplication, api_renewLodge_license, api_getLodgeApplicationDetailsToRenew } = AdvertisementApiList()
    const notify = (toastData, type) => {
        toast.dismiss();
        if (type == 'success') {
            toast.success(toastData)
        }
        if (type == 'error') {
            toast.error(toastData)
        }
    };

    ///////////{*** COLLECTING ALL FORM DATA***}/////////
    const collectAllFormData = (key, formData, reviewIdName) => {
        console.log('prev of all Data', allFormData)
        console.log("review name by id in index...", reviewIdName)
        setAllFormData({ ...allFormData, [key]: formData })

        if (key == 'lodgeForm') {
            console.log("data collecting by key", key, 'formData', formData, 'reviewData', reviewIdName)
            setreviewData({ ...reviewData, [key]: reviewIdName })
        }
        else {
            console.log('data not in review ===', key, '===', formData, 'preview...', reviewIdName)
            setreviewData({ ...reviewData, [key]: formData })
        }
    }
    console.log("all form data in index", allFormData)
    console.log("all form data in index for doc", allFormData?.selfAdvertisementDoc?.[0])


    ///// SUBMIT FORM /////
    const submitButtonToggle = () => {
        console.log('final form ready to submit...', allFormData)
        submitLodgeForm()
    }

    const submitLodgeForm = () => {

        setisLoading(true)

        let url
        let requestBody
        if (type == 'newapply') {
            url = api_postLodgeApplication  //new apply
            requestBody = {
                // ulbId: allFormData?.selfAdvertisement?.ulb,
                ulbId: 2,
                rule: allFormData?.lodgeForm?.rule,
                applicantName: allFormData?.lodgeForm?.applicantName,
                fatherName: allFormData?.lodgeForm?.fatherName,
                mobile: allFormData?.lodgeForm?.mobileNo,
                email: allFormData?.lodgeForm?.email,
                residentialAddress: allFormData?.lodgeForm?.residenceAddress,
                residentialWardId: allFormData?.lodgeForm?.residenceWardNo,
                permanentAddress: allFormData?.lodgeForm?.permanentAddress,
                permanentWardId: allFormData?.lodgeForm?.permanentWardNo,
                licenseYear: allFormData?.lodgeForm?.licenseYear,
                entityName: allFormData?.lodgeForm?.entityName,
                entityAddress: allFormData?.lodgeForm?.entityAddress,
                entityWardId: allFormData?.lodgeForm?.entityWardNo,
                holdingNo: allFormData?.lodgeForm?.holdingNo,
                tradeLicenseNo: allFormData?.lodgeForm?.tradeLicenseNo,
                longitude: allFormData?.lodgeForm?.longitude,
                latitude: allFormData?.lodgeForm?.latitude,
                organizationType: allFormData?.lodgeForm?.organizationType,
                landDeedType: allFormData?.lodgeForm?.landDeedType,
                waterSupplyType: allFormData?.lodgeForm?.waterSupplyType,
                electricityType: allFormData?.lodgeForm?.electricityType,
                securityType: allFormData?.lodgeForm?.securityType,
                lodgeType: allFormData?.lodgeForm?.lodgeType,
                messType: allFormData?.lodgeForm?.messType,
                cctvCamera: allFormData?.lodgeForm?.noOfCctv,
                noOfBeds: allFormData?.lodgeForm?.noOfBeds,
                noOfRooms: allFormData?.lodgeForm?.noOfRooms,
                fireExtinguisher: allFormData?.lodgeForm?.noFireExtinguishers,
                entryGate: allFormData?.lodgeForm?.noEntryGates,
                exitGate: allFormData?.lodgeForm?.noExitGate,
                twoWheelersParking: allFormData?.lodgeForm?.noTwoWheelerParking,
                fourWheelersParking: allFormData?.lodgeForm?.noFourWheelerParking,
                aadharCard: allFormData?.lodgeForm?.aadharNo,
                panCard: allFormData?.lodgeForm?.panNo,

                documents: allFormData?.lodgeDoc
            }
        }
        if (type == 'renew') {
            url = api_renewLodge_license;//"renew url";
            requestBody = {
                applicationId: id,
                // ulbId: allFormData?.selfAdvertisement?.ulb,
                ulbId: 2,
                rule: allFormData?.lodgeForm?.rule,
                applicantName: allFormData?.lodgeForm?.applicantName,
                fatherName: allFormData?.lodgeForm?.fatherName,
                mobile: allFormData?.lodgeForm?.mobileNo,
                email: allFormData?.lodgeForm?.email,
                residentialAddress: allFormData?.lodgeForm?.residenceAddress,
                residentialWardId: allFormData?.lodgeForm?.residenceWardNo,
                permanentAddress: allFormData?.lodgeForm?.permanentAddress,
                permanentWardId: allFormData?.lodgeForm?.permanentWardNo,
                licenseYear: allFormData?.lodgeForm?.licenseYear,
                entityName: allFormData?.lodgeForm?.entityName,
                entityAddress: allFormData?.lodgeForm?.entityAddress,
                entityWardId: allFormData?.lodgeForm?.entityWardNo,
                holdingNo: allFormData?.lodgeForm?.holdingNo,
                tradeLicenseNo: allFormData?.lodgeForm?.tradeLicenseNo,
                longitude: allFormData?.lodgeForm?.longitude,
                latitude: allFormData?.lodgeForm?.latitude,
                organizationType: allFormData?.lodgeForm?.organizationType,
                landDeedType: allFormData?.lodgeForm?.landDeedType,
                waterSupplyType: allFormData?.lodgeForm?.waterSupplyType,
                electricityType: allFormData?.lodgeForm?.electricityType,
                securityType: allFormData?.lodgeForm?.securityType,
                lodgeType: allFormData?.lodgeForm?.lodgeType,
                messType: allFormData?.lodgeForm?.messType,
                cctvCamera: allFormData?.lodgeForm?.noOfCctv,
                noOfBeds: allFormData?.lodgeForm?.noOfBeds,
                noOfRooms: allFormData?.lodgeForm?.noOfRooms,
                fireExtinguisher: allFormData?.lodgeForm?.noFireExtinguishers,
                entryGate: allFormData?.lodgeForm?.noEntryGates,
                exitGate: allFormData?.lodgeForm?.noExitGate,
                twoWheelersParking: allFormData?.lodgeForm?.noTwoWheelerParking,
                fourWheelersParking: allFormData?.lodgeForm?.noFourWheelerParking,
                aadharCard: allFormData?.lodgeForm?.aadharNo,
                panCard: allFormData?.lodgeForm?.panNo,

                documents: allFormData?.lodgeDoc
            }
        }
        console.log('request body...', requestBody)
        axios.post(`${url}`, requestBody, ApiHeaderAdvrt())
            .then(function (response) {
                console.log('response after data submitted', response.data.data)
                setresponseScreen(response.data.data)
                setisLoading(false)
                notify("submitted successfully", "success")
            })
            .catch(function (error) {
                console.log('errorrr.... ', error);
                setisLoading(false)
                notify(error.response.data.message`${"failed to submit"}`, "error")

            })
    }

    // passing values in components
    const values = {
        setFormIndex: setFormIndex,
        showLoader: showLoader,
        collectFormDataFun: collectAllFormData,
        toastFun: notify,
        type: type,
        existingApplicationDetails: existingApplicationDetails,
    }

    useEffect(() => {
        setapplicationType(type)
        if (type == 'renew') {
            fetchApplicationDetails()
        }
    }, [])
    const fetchApplicationDetails = () => {
        seterroState(false)
        setisLoading(true)
        let requestBody = {
            applicationId: id,
        }

        console.log('renew application id', requestBody)
        axios.post(`${api_getLodgeApplicationDetailsToRenew}`, requestBody, ApiHeaderAdvrt())
            .then(function (response) {
                console.log('dharamshala application detail for renew application.....', response)
                setexistingApplicationDetails(response.data.data)
                setisLoading(false)
            })
            .catch(function (error) {
                console.log('==2 details by id error...', error)
                seterroState(true)
                setisLoading(false)
            })
    }


    console.log("response screen", responseScreen)
    if (responseScreen?.status == true) {
        return (
            <>
                <AdvrtSuccessScreen responseScreenData={responseScreen} />
            </>
        )
    }

    return (
        <>
            <ToastContainer position="top-right"
                autoClose={2000} />

            <div className='overflow-x-clip p-4 mt-3 container mx-auto h-screen'>
                <div className='grid grid-cols-1 md:grid-cols-2 lg:grid-cols-2 bg-white p-2 rounded-md shadow-md shadow-violet-200  '>
                    <div className=''>
                        <div className='flex flex-row '>
                            <h1 className='text-2xl ml-4 text-gray-600 font-sans font-semibold'>LODGE HALL REGISTRATION APPLICATION</h1>
                        </div>
                        <h1 className='text-xs ml-3 p-1 text-gray-600 font-sans'>
                            You Can Get License To Advertise Your Business Name On Your Shop
                        </h1>
                    </div>
                    <div>
                        <div className='flex flex-row mt-2 float-right'>
                            <span className='text-md font-bold md:text-xl text-violet-600 text-center  transition-all animate-wiggle'>&emsp; <strong className='text-2xl text-violet-600 '>{3 - formIndex}
                            </strong> more screen</span>
                            <img src='https://cdn-icons-png.flaticon.com/512/2376/2376320.png' className='h-10 mr-4  opacity-80 float-right ml-4' />
                        </div>
                    </div>
                </div>
                <div className={`${formIndex == 1 ? 'translate-x-0' : 'translate-x-full'} transition-all`}><LodgeForm values={values} /></div>
                <div className={`${formIndex == 2 ? 'translate-x-0' : 'translate-x-full'} transition-all`}><LodgeDocForm values={values} /></div>
                <div className={`${formIndex == 3 ? 'translate-x-0' : 'translate-x-full'} transition-all`}><ReviewLodge values={values} reviewIdNameData={reviewData} allFormData={allFormData} submitFun={submitButtonToggle} /></div>
            </div>
        </>
    )
}

export default LodgeIndex