import React from 'react'
import { nullToNA } from '../../../../Components/Common/PowerUps/PowerupFunctions'

function ReviewPrivateLandApplication(props) {

    const { setFormIndex, showLoader, collectFormDataFun, toastFun } = props?.values

    let labelStyle = "mt-6  text-xs text-gray-600"
    let inputStyle = "mt-6  text-md text-gray-800 font-bold"

    const submitForm = () => {
        props.submitFun()
    }
    console.log("data in allform data...", props.allFormData)
    console.log("data in review form ...", props.reviewIdNameData)


    return (
        <div className='absolute w-full ml-4 top-2  bg-violet-50'>
            <div className='grid grid-cols-1 md:grid-cols-12 lg:grid-cols-12 gap-4 container mx-auto  '>
                <div className='col-span-8 border border-dashed border-violet-800 '>
                    <h1 className='text-center p-3 mb-2 bg-white text-gray-600 font-sans font-semibold'>APPLICATION DETAILS</h1>
                    <div className='grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 p-2 ml-12'>
                        <div className=''>
                            <p className={`${labelStyle}`}>Ulb</p>
                            <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.privateLand?.ulb || props?.reviewIdNameData?.privateLand?.ulb_name)}</span>
                        </div>
                        <div className=''>
                            <p className={`${labelStyle}`}>License From</p>
                            <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.privateLand?.licenseFrom || props?.reviewIdNameData?.privateLand?.license_from)}</span>
                        </div>
                        <div className=''>
                            <p className={`${labelStyle}`}>License To  </p>
                            <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.privateLand?.licenseTo || props?.reviewIdNameData?.privateLand?.license_to)}</span>
                        </div>
                        <div className=''>
                            <p className={`${labelStyle}`}>Applicant</p>
                            <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.privateLand?.applicantName || props?.reviewIdNameData?.privateLand?.applicant)}</span>
                        </div>
                    </div>
                    <div className='grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 p-2 ml-12'>
                        <div className=''>
                            <p className={`${labelStyle}`}>Residence Address</p>
                            <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.privateLand?.residenceAddress || props?.reviewIdNameData?.privateLand?.residence_address)}</span>
                        </div>
                        <div className=''>
                            <p className={`${labelStyle}`}>Residence Ward No </p>
                            <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.privateLand?.residenceWardNo || props?.reviewIdNameData?.privateLand?.ward_id)}</span>
                        </div>
                        <div className=''>
                            <p className={`${labelStyle}`}>Permanent Address</p>
                            <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.privateLand?.permanentAddress || props?.reviewIdNameData?.privateLand?.permanent_address)}</span>
                        </div>
                        <div className=''>
                            <p className={`${labelStyle}`}>Permanent Ward No</p>
                            <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.privateLand?.permanentWardNo || props?.reviewIdNameData?.privateLand?.permanent_ward_id)}</span>
                        </div>
                    </div>
                    <div className='grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 p-2 ml-12'>

                        <div className=''>
                            <p className={`${labelStyle}`}>Holding no.</p>
                            <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.privateLand?.holdingNo || props?.reviewIdNameData?.privateLand?.holding_no)}</span>
                        </div>

                        <div className=''>
                            <p className={`${labelStyle}`}>Mobile No.</p>
                            <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.privateLand?.mobileNo || props?.reviewIdNameData?.privateLand?.mobile_no)}</span>
                        </div>
                        <div className=''>
                            <p className={`${labelStyle}`}>Aadhar No. </p>
                            <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.privateLand?.aadharNo || props?.reviewIdNameData?.privateLand?.aadhar_no)}</span>
                        </div>

                        <div className=''>
                            <p className={`${labelStyle}`}>E-mail </p>
                            <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.privateLand?.email || props?.reviewIdNameData?.privateLand?.email)}</span>
                        </div>
                    </div>
                    <div className='grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 p-2 ml-12'>

                        <div className=''>
                            <p className={`${labelStyle}`}>Entity Name</p>
                            <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.privateLand?.entityName || props?.reviewIdNameData?.privateLand?.entity_name)}</span>
                        </div>
                        <div className=''>
                            <p className={`${labelStyle}`}>Entity Address</p>
                            <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.privateLand?.entityAddress || props?.reviewIdNameData?.privateLand?.entity_address)}</span>
                        </div>
                        <div className=''>
                            <p className={`${labelStyle}`}>Entity Ward</p>
                            <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.privateLand?.entityWard || props?.reviewIdNameData?.privateLand?.entity_ward_id)}</span>
                        </div><div className=''>
                            <p className={`${labelStyle}`}>Gst No.</p>
                            <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.privateLand?.gstNo || props?.reviewIdNameData?.privateLand?.gst_no)}</span>
                        </div>
                    </div>
                    <div className='grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 p-2 ml-12'>

                        <div className=''>
                            <p className={`${labelStyle}`}>Brand Display Name</p>
                            <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.privateLand?.brandDisplayeName || props?.reviewIdNameData?.privateLand?.brand_display_name)}</span>
                        </div>
                        <div className=''>
                            <p className={`${labelStyle}`}>Brand Displayed Address</p>
                            <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.privateLand?.brandDisplayeAddress || props?.reviewIdNameData?.privateLand?.brand_display_address)}</span>
                        </div>
                        <div className=''>
                            <p className={`${labelStyle}`}>Total Display Area</p>
                            <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.privateLand?.totalDisplayArea || props?.reviewIdNameData?.privateLand?.display_area)}</span>
                        </div><div className=''>
                            <p className={`${labelStyle}`}>Display Type </p>
                            <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.privateLand?.displayType || props?.reviewIdNameData?.privateLand?.display_type)}</span>
                        </div>
                    </div>
                    <div className='grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 p-2 ml-12'>
                        <div className=''>
                            <p className={`${labelStyle}`}>Holding No. of Brand Display Address</p>
                            <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.privateLand?.holdingNoBrandDisplay || props?.reviewIdNameData?.privateLand?.holdingNoBrandDisplay)}</span>
                        </div>
                        <div className=''>
                            <p className={`${labelStyle}`}>No. of Hoarding</p>
                            <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.privateLand?.noOfHoarding || props?.reviewIdNameData?.privateLand?.no_of_hoardings)}</span>
                        </div>
                        <div className=''>
                            <p className={`${labelStyle}`}>Trade License No.</p>
                            <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.privateLand?.tradeLicenseNo || props?.reviewIdNameData?.privateLand?.trade_license_no)}</span>
                        </div>
                        <div className=''>
                            <p className={`${labelStyle}`}>Installation Location</p>
                            <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.privateLand?.installationLocation || props?.reviewIdNameData?.privateLand?.installation_location)}</span>
                        </div>
                    </div>
                    <div className='grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 p-2 ml-12'>

                        <div className=''>
                            <p className={`${labelStyle}`}>Longitude</p>
                            <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.privateLand?.longitude || props?.reviewIdNameData?.privateLand?.longitude)}</span>
                        </div>
                        <div className=''>
                            <p className={`${labelStyle}`}>Latitude</p>
                            <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.privateLand?.latitude || props?.reviewIdNameData?.privateLand?.latitude)}</span>
                        </div>
                        <div className=''>
                            <p className={`${labelStyle}`}>Typolgy Type</p>
                            <span className={`${inputStyle}`}>{nullToNA(props?.reviewIdNameData?.privateLand?.typology || props?.reviewIdNameData?.privateLand?.typology)}</span>
                        </div>
                    </div>
                </div>
                {/* document */}
                <div className='col-span-4 border border-dashed border-violet-800 '>
                    <h1 className='text-center p-3 mb-2 bg-white text-gray-600 font-sans font-semibold '>DOCUMENTS UPLOADED</h1>
                    <div className='grid grid-cols-1 md:grid-cols-1 lg:grid-cols-1 p-2 ml-12'>
                        {props?.reviewIdNameData?.privateLandDoc?.map((data, index) => (
                            <div className=''>
                                <>
                                    <p className={`${labelStyle}`}>{data?.docCode} Document</p>
                                    <div className='flex -mt-2'>
                                        <span className={`${inputStyle} flex-1`}>{data?.image?.name}</span>
                                        <span className={`mt-4  flex-1 float-right`}><img src="https://cdn-icons-png.flaticon.com/512/1829/1829589.png" alt="Preview Image" className={` w-8`} /></span>
                                    </div>
                                </>
                            </div>
                        ))}

                    </div>
                </div>
            </div>
            <div className="grid grid-cols-12 w-full p-3 ">
                <div className='md:pl-0 col-span-6'>
                    <button type="button" class="py-2 px-4 text-xs inline-block text-center mb-3 rounded leading-5 text-gray-100 bg-indigo-500 border border-indigo-500 hover:text-white hover:bg-indigo-600 hover:ring-0 hover:border-indigo-600 focus:bg-indigo-600 focus:border-indigo-600 focus:outline-none focus:ring-0" onClick={() => setFormIndex(2)}>back</button>

                </div>
                <div className='col-span-6'>
                    <button type="submit" class="float-right py-2 px-4 text-xs inline-block text-center mb-3 rounded leading-5 text-gray-100 bg-green-500 border border-green-500 hover:text-white hover:bg-green-600 hover:ring-0 hover:border-green-600 focus:bg-green-600 focus:border-green-600 focus:outline-none focus:ring-0" onClick={submitForm}>Submit</button>

                </div>
            </div>
        </div>
    )
}

export default ReviewPrivateLandApplication