import React, { useEffect, useState } from 'react'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import axios from 'axios'
import DharamshalaForm from './DharamshalaForm';
import DharamshalaDocForm from './DharamshalaDocForm';
import ReviewDharamshalaForm from './ReviewDharamshalaForm';
import AdvertisementApiList from '../../../AdvertisementApiList';
import ApiHeaderAdvrt from '../../../ApiHeaderAdvrt';
import Loader from '../../../Advertisement/Loader';
import BackButton from '../../../Advertisement/BackButton';
import AdvrtSuccessScreen from '../../../Advertisement/AdvrtSuccessScreen';
import BrandLoader from '../../../../../Components/Common/BrandLoader';
import CommonModal from '../../../../../Components/GlobalData/CommonModal';
import ServerErrorCard from '../../../ServerErrorCard';
import { useParams } from 'react-router-dom';


function DharamshalaIndex() {
    const [formIndex, setFormIndex] = useState(1) //formindex specifies type of form  at index 1 ...
    const [allFormData, setAllFormData] = useState([])
    const [responseScreen, setresponseScreen] = useState()
    const [reviewData, setreviewData] = useState({})
    const [show, setshow] = useState(false)
    const [existingApplicationDetails, setexistingApplicationDetails] = useState()
    const [applicationType, setapplicationType] = useState()
    const [isLoading, setisLoading] = useState(false);
    const [erroState, seterroState] = useState(false);


    const { id, type } = useParams();
    console.log("self advertisement param", id)
    console.log("self advertisement param", type)

    const showLoader = (val) => {
        setshow(val);
    }

    const { api_postDharamshalaApplication, api_renewDharamshala_license, api_getDharamshalaApplicationDetailsToRenew } = AdvertisementApiList()
    const notify = (toastData, type) => {
        toast.dismiss();
        if (type == 'success') {
            toast.success(toastData)
        }
        if (type == 'error') {
            toast.error(toastData)
        }
    };

    ///////////{*** COLLECTING ALL FORM DATA***}/////////
    const collectAllFormData = (key, formData, reviewIdName) => {
        console.log('prev of all Data', allFormData)
        console.log("review name by id in index...", reviewIdName)
        setAllFormData({ ...allFormData, [key]: formData })

        if (key == 'dharamshala') {
            console.log("data collecting by key", key, 'formData', formData, 'reviewData', reviewIdName)
            setreviewData({ ...reviewData, [key]: reviewIdName })
        }
        else {
            console.log('data not in review ===', key, '===', formData, 'preview...', reviewIdName)
            setreviewData({ ...reviewData, [key]: formData })
        }
    }
    console.log("all form data in index", allFormData)
    console.log("all form data in index for doc", allFormData?.selfAdvertisementDoc)


    ///// SUBMIT FORM /////
    const submitButtonToggle = () => {
        console.log('final form ready to submit...', allFormData)
        submitDharamshalaForm()
    }

    const submitDharamshalaForm = () => {
        setisLoading(true)

        let url
        let requestBody
        if (type == 'newapply') {
            url = api_postDharamshalaApplication  //new apply
            requestBody = {
                ulbId: 2,
                rule: allFormData?.dharamshala?.rule,
                applicantName: allFormData?.dharamshala?.applicantName,
                fatherName: allFormData?.dharamshala?.fatherName,
                mobile: allFormData?.dharamshala?.mobileNo,
                email: allFormData?.dharamshala?.email,
                residentialAddress: allFormData?.dharamshala?.residenceAddress,
                residentialWardId: allFormData?.dharamshala?.residenceWardNo,
                permanentAddress: allFormData?.dharamshala?.permanentAddress,
                permanentWardId: allFormData?.dharamshala?.permanentWardNo,
                licenseYear: allFormData?.dharamshala?.licenseYear,
                entityName: allFormData?.dharamshala?.entityName,
                entityAddress: allFormData?.dharamshala?.entityAddress,
                entityWardId: allFormData?.dharamshala?.entityWardNo,
                holdingNo: allFormData?.dharamshala?.holdingNo,
                tradeLicenseNo: allFormData?.dharamshala?.tradeLicenseNo,
                longitude: allFormData?.dharamshala?.longitude,
                latitude: allFormData?.dharamshala?.latitude,
                floorArea: allFormData?.dharamshala?.floorArea,
                organizationType: allFormData?.dharamshala?.organizationType,
                landDeedType: allFormData?.dharamshala?.landDeedType,
                waterSupplyType: allFormData?.dharamshala?.waterSupplyType,
                electricityType: allFormData?.dharamshala?.electricityType,
                securityType: allFormData?.dharamshala?.securityType,
                cctvCamera: allFormData?.dharamshala?.noOfCctv,
                noOfBeds: allFormData?.dharamshala?.noOfBeds,
                noOfRooms: allFormData?.dharamshala?.noOfRooms,
                fireExtinguisher: allFormData?.dharamshala?.noFireExtinguishers,
                entryGate: allFormData?.dharamshala?.noEntryGates,
                exitGate: allFormData?.dharamshala?.noExitGate,
                twoWheelersParking: allFormData?.dharamshala?.noTwoWheelerParking,
                fourWheelersParking: allFormData?.dharamshala?.noFourWheelerParking,
                aadharCard: allFormData?.dharamshala?.aadharNo,
                panCard: allFormData?.dharamshala?.panNo,
                documents: allFormData?.dharamshalaDoc,
            }
        }
        if (type == 'renew') {
            url = api_renewDharamshala_license;//"renew url";
            requestBody = {
                applicationId: id,
                // ulbId: allFormData?.selfAdvertisement?.ulb,
                ulbId: 2,
                rule: allFormData?.dharamshala?.rule,
                applicantName: allFormData?.dharamshala?.applicantName,
                fatherName: allFormData?.dharamshala?.fatherName,
                mobile: allFormData?.dharamshala?.mobileNo,
                email: allFormData?.dharamshala?.email,
                residentialAddress: allFormData?.dharamshala?.residenceAddress,
                residentialWardId: allFormData?.dharamshala?.residenceWardNo,
                permanentAddress: allFormData?.dharamshala?.permanentAddress,
                permanentWardId: allFormData?.dharamshala?.permanentWardNo,
                licenseYear: allFormData?.dharamshala?.licenseYear,
                entityName: allFormData?.dharamshala?.entityName,
                entityAddress: allFormData?.dharamshala?.entityAddress,
                entityWardId: allFormData?.dharamshala?.entityWardNo,
                holdingNo: allFormData?.dharamshala?.holdingNo,
                tradeLicenseNo: allFormData?.dharamshala?.tradeLicenseNo,
                longitude: allFormData?.dharamshala?.longitude,
                latitude: allFormData?.dharamshala?.latitude,
                floorArea: allFormData?.dharamshala?.floorArea,
                organizationType: allFormData?.dharamshala?.organizationType,
                landDeedType: allFormData?.dharamshala?.landDeedType,
                waterSupplyType: allFormData?.dharamshala?.waterSupplyType,
                electricityType: allFormData?.dharamshala?.electricityType,
                securityType: allFormData?.dharamshala?.securityType,
                cctvCamera: allFormData?.dharamshala?.noOfCctv,
                noOfBeds: allFormData?.dharamshala?.noOfBeds,
                noOfRooms: allFormData?.dharamshala?.noOfRooms,
                fireExtinguisher: allFormData?.dharamshala?.noFireExtinguishers,
                entryGate: allFormData?.dharamshala?.noEntryGates,
                exitGate: allFormData?.dharamshala?.noExitGate,
                twoWheelersParking: allFormData?.dharamshala?.noTwoWheelerParking,
                fourWheelersParking: allFormData?.dharamshala?.noFourWheelerParking,
                aadharCard: allFormData?.dharamshala?.aadharNo,
                panCard: allFormData?.dharamshala?.panNo,
                documents: allFormData?.dharamshalaDoc,
            }
        }

        console.log('request body...', requestBody)
        axios.post(`${url}`, requestBody, ApiHeaderAdvrt())
            .then(function (response) {
                console.log('response after data submitted', response.data.data)
                setresponseScreen(response.data.data)
                setisLoading(false)
                notify("submitted successfully", "success")
            })
            .catch(function (error) {
                console.log('errorrr.... ', error);
                setisLoading(false)
                notify("failed to submit", "error")

            })
    }

    // passing values in components
    const values = {
        setFormIndex: setFormIndex,
        showLoader: showLoader,
        collectFormDataFun: collectAllFormData,
        toastFun: notify,
        type: type,
        existingApplicationDetails: existingApplicationDetails,
    }

    useEffect(() => {
        setapplicationType(type)
        if (type == 'renew') {
            fetchApplicationDetails()
        }
    }, [])
    const fetchApplicationDetails = () => {
        seterroState(false)
        setisLoading(true)
        let requestBody = {
            applicationId: id,
        }

        console.log('renew application id', requestBody)
        axios.post(`${api_getDharamshalaApplicationDetailsToRenew}`, requestBody, ApiHeaderAdvrt())
            .then(function (response) {
                console.log('dharamshala application detail for renew application.....', response)
                setexistingApplicationDetails(response.data.data)
                setisLoading(false)
            })
            .catch(function (error) {
                console.log('==2 details by id error...', error)
                seterroState(true)
                setisLoading(false)
            })
    }

    console.log("response screen", responseScreen)
    if (responseScreen?.status == true) {
        return (
            <>
                <AdvrtSuccessScreen responseScreenData={responseScreen} />
            </>
        )
    }

    if (isLoading) {
        return (
            <>
                <BrandLoader />
            </>
        )
    }
    if (erroState) {
        return (
            <CommonModal>
                <ServerErrorCard title="Server is busy" desc="Server is too busy to respond. Please try again later." buttonText="View Dashboard" buttonUrl="/propertyDashboard" />
            </CommonModal>
        )
    }

    return (
        <>
            <div className=''>
                <ToastContainer position="top-right"
                    autoClose={2000} />

                <div className='overflow-x-clip  p-4 mt-3 container mx-auto h-screen'>
                    <div className='grid grid-cols-1 md:grid-cols-2 lg:grid-cols-2 bg-white p-2 rounded-md shadow-md shadow-violet-200  '>
                        <div className=''>
                            <div className='flex flex-row '>
                                <h1 className='text-2xl ml-4 text-gray-600 font-sans font-semibold'>DHARAMSHALA REGISTRATION APPLICATION</h1>
                            </div>
                            <h1 className='text-xs ml-3 p-1 text-gray-600 font-sans'>
                                You Can Get License To Advertise Your Business Name On Your Shop
                            </h1>
                        </div>
                        <div>
                            <div className='flex flex-row mt-2 float-right'>
                                <span className='text-md font-bold md:text-xl text-violet-600 text-center  transition-all animate-wiggle '>&emsp; <strong className='text-2xl text-violet-600 '>{3 - formIndex}
                                </strong> more screen</span>
                                <img src='https://cdn-icons-png.flaticon.com/512/2376/2376320.png' className='h-10 mr-4  opacity-80 float-right  ml-4' />
                            </div>
                        </div>
                    </div>

                    <div className={`${formIndex == 1 ? 'translate-x-0' : 'translate-x-full'} transition-all`}><DharamshalaForm values={values} /></div>
                    <div className={`${formIndex == 2 ? 'translate-x-0' : 'translate-x-full'} transition-all`}><DharamshalaDocForm values={values} /></div>
                    <div className={`${formIndex == 3 ? 'translate-x-0' : 'translate-x-full'} transition-all`}><ReviewDharamshalaForm values={values} reviewIdNameData={reviewData} allFormData={allFormData} submitFun={submitButtonToggle} /></div>
                </div>
            </div>
        </>
    )
}

export default DharamshalaIndex