import React from 'react'

function ReviewDharamshalaForm(props) {
    const { setFormIndex, showLoader, collectFormDataFun, toastFun } = props?.values

    let labelStyle = "mt-6 -ml-7 text-xs text-gray-600"
    let inputStyle = "mt-6 -ml-7 text-md text-gray-700 font-bold"

    const submitForm = () => {
        props.submitFun()
    }
    console.log("data in review form...", props?.allFormData)
    console.log("data in review form documents...", props?.reviewIdNameData)
    return (
        <>
            <div className='absolute w-full ml-4 top-4 '>
                <div className='grid grid-cols-1 md:grid-cols-12 lg:grid-cols-12 gap-4 container mx-auto  '>
                    <div className='col-span-8 border border-dashed border-violet-800 '>
                        <h1 className='text-center p-3 mb-2 bg-white text-gray-600 font-sans font-semibold'>APPLICATION DETAILS</h1>
                        <div className='grid grid-cols-1 md:grid-cols-4 lg:grid-cols-4 p-2 ml-12'>
                            <div className=''>
                                <p className={`${labelStyle}`}>License Year</p>
                                <span className={`${inputStyle}`}>{props?.reviewIdNameData?.dharamshala
                                    ?.licenseYear || props?.reviewIdNameData?.dharamshala
                                        ?.licenseYear}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>Applicant Name</p>
                                <span className={`${inputStyle}`}>{props?.reviewIdNameData?.dharamshala
                                    ?.applicantName || props?.reviewIdNameData?.dharamshala
                                        ?.applicant}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>Father Name</p>
                                <span className={`${inputStyle}`}>{props?.reviewIdNameData?.dharamshala
                                    ?.fatherName || props?.reviewIdNameData?.dharamshala
                                        ?.father}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>Residence Address</p>
                                <span className={`${inputStyle}`}>{props?.reviewIdNameData?.dharamshala
                                    ?.residenceAddress || props?.reviewIdNameData?.dharamshala
                                        ?.residential_address}</span>
                            </div>
                        </div>
                        <div className='grid grid-cols-1 md:grid-cols-4 lg:grid-cols-4 p-2 ml-12'>
                            <div className=''>
                                <p className={`${labelStyle}`}>Residence Ward No.</p>
                                <span className={`${inputStyle}`}>{props?.reviewIdNameData?.dharamshala
                                    ?.residenceWardNo || props?.reviewIdNameData?.dharamshala
                                        ?.resident_ward_name}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>Permanent Address</p>
                                <span className={`${inputStyle}`}>{props?.reviewIdNameData?.dharamshala
                                    ?.permanentAddress || props?.reviewIdNameData?.dharamshala
                                        ?.permanent_address}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>Permanent Ward No.</p>
                                <span className={`${inputStyle}`}>{props?.reviewIdNameData?.dharamshala
                                    ?.permanentWardNo || props?.reviewIdNameData?.dharamshala
                                        ?.permanent_ward_name}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>Mobile No.</p>
                                <span className={`${inputStyle}`}>{props?.reviewIdNameData?.dharamshala
                                    ?.mobileNo || props?.reviewIdNameData?.dharamshala
                                        ?.mobile}</span>
                            </div>
                        </div>
                        <div className='grid grid-cols-1 md:grid-cols-4 lg:grid-cols-4 p-2 ml-12'>

                            <div className=''>
                                <p className={`${labelStyle}`}>E-mail</p>
                                <span className={`${inputStyle}`}>{props?.reviewIdNameData?.dharamshala
                                    ?.email || props?.reviewIdNameData?.dharamshala
                                        ?.email}</span>
                            </div>

                            <div className=''>
                                <p className={`${labelStyle}`}>Aadhar Card No.</p>
                                <span className={`${inputStyle}`}>{props?.reviewIdNameData?.dharamshala
                                    ?.aadharNo || props?.reviewIdNameData?.dharamshala
                                        ?.aadhar_card}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>Pan Card No.</p>
                                <span className={`${inputStyle}`}>{props?.reviewIdNameData?.dharamshala
                                    ?.panNo || props?.reviewIdNameData?.dharamshala
                                        ?.pan_card}</span>
                            </div>

                            <div className=''>
                                <p className={`${labelStyle}`}>Entity Name</p>
                                <span className={`${inputStyle}`}>{props?.reviewIdNameData?.dharamshala
                                    ?.entityName || props?.reviewIdNameData?.dharamshala
                                        ?.entity_name}</span>
                            </div>
                        </div>
                        <div className='grid grid-cols-1 md:grid-cols-4 lg:grid-cols-4 p-2 ml-12'>

                            <div className=''>
                                <p className={`${labelStyle}`}>Entity Address</p>
                                <span className={`${inputStyle}`}>{props?.reviewIdNameData?.dharamshala
                                    ?.entityAddress || props?.reviewIdNameData?.dharamshala
                                        ?.entity_address}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>Entity Ward No.</p>
                                <span className={`${inputStyle}`}>{props?.reviewIdNameData?.dharamshala
                                    ?.entityWardNo || props?.reviewIdNameData?.dharamshala
                                        ?.entity_ward_name}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>Holding No.</p>
                                <span className={`${inputStyle}`}>{props?.reviewIdNameData?.dharamshala
                                    ?.holdingNo || props?.reviewIdNameData?.dharamshala
                                        ?.holding_no}</span>
                            </div><div className=''>
                                <p className={`${labelStyle}`}>Trade License No.</p>
                                <span className={`${inputStyle}`}>{props?.reviewIdNameData?.dharamshala
                                    ?.tradeLicenseNo || props?.reviewIdNameData?.dharamshala
                                        ?.trade_license_no}</span>
                            </div>
                        </div>
                        <div className='grid grid-cols-1 md:grid-cols-4 lg:grid-cols-4 p-2 ml-12'>
                            <div className=''>
                                <p className={`${labelStyle}`}>Floor Area</p>
                                <span className={`${inputStyle}`}>{props?.reviewIdNameData?.dharamshala
                                    ?.floorArea || props?.reviewIdNameData?.dharamshala
                                        ?.floor_area}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>Longitude </p>
                                <span className={`${inputStyle}`}>{props?.reviewIdNameData?.dharamshala
                                    ?.longitude || props?.reviewIdNameData?.dharamshala
                                        ?.longitude}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>Latitude  </p>
                                <span className={`${inputStyle}`}>{props?.reviewIdNameData?.dharamshala
                                    ?.latitude || props?.reviewIdNameData?.dharamshala
                                        ?.latitude}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>Organization Type</p>
                                <span className={`${inputStyle}`}>{props?.reviewIdNameData?.dharamshala?.organizationType || props?.reviewIdNameData?.dharamshala
                                    ?.organization_type_name}</span>
                            </div>
                        </div>
                        <div className='grid grid-cols-1 md:grid-cols-4 lg:grid-cols-4 p-2 ml-12'>

                            <div className=''>
                                <p className={`${labelStyle}`}>Land Deed Type  </p>
                                <span className={`${inputStyle}`}>{props?.reviewIdNameData?.dharamshala
                                    ?.landDeedType || props?.reviewIdNameData?.dharamshala
                                        ?.land_deed_type_name}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>Water Supply Type </p>
                                <span className={`${inputStyle}`}>{props?.reviewIdNameData?.dharamshala
                                    ?.waterSupplyType || props?.reviewIdNameData?.dharamshala
                                        ?.water_supply_type_name}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>Electricity Type</p>
                                <span className={`${inputStyle}`}>{props?.reviewIdNameData?.dharamshala
                                    ?.electricityType || props?.reviewIdNameData?.dharamshala
                                        ?.electricity_type_name}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>Security Type</p>
                                <span className={`${inputStyle}`}>{props?.reviewIdNameData?.dharamshala
                                    ?.securityType || props?.reviewIdNameData?.dharamshala
                                        ?.security_type_name}</span>
                            </div>

                        </div>
                        <div className='grid grid-cols-1 md:grid-cols-4 lg:grid-cols-4 p-2 ml-12'>
                            <div className=''>
                                <p className={`${labelStyle}`}>No of Beds</p>
                                <span className={`${inputStyle}`}>{props?.reviewIdNameData?.dharamshala
                                    ?.noOfBeds || props?.reviewIdNameData?.dharamshala
                                        ?.cctv_camera}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>No of Rooms</p>
                                <span className={`${inputStyle}`}>{props?.reviewIdNameData?.dharamshala
                                    ?.noOfRooms || props?.reviewIdNameData?.dharamshala
                                        ?.cctv_camera}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>No of CCTV Camera</p>
                                <span className={`${inputStyle}`}>{props?.reviewIdNameData?.dharamshala
                                    ?.noOfCctv || props?.reviewIdNameData?.dharamshala
                                        ?.cctv_camera}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>No. of Fire Extinguishers </p>
                                <span className={`${inputStyle}`}>{props?.reviewIdNameData?.dharamshala
                                    ?.noFireExtinguishers || props?.reviewIdNameData?.dharamshala
                                        ?.fire_extinguisher}</span>
                            </div>
                          
                        </div>
                        <div className='grid grid-cols-1 md:grid-cols-4 lg:grid-cols-4 p-2 ml-12'>
                        <div className=''>
                                <p className={`${labelStyle}`}>No. of Entry Gates</p>
                                <span className={`${inputStyle}`}>{props?.reviewIdNameData?.dharamshala
                                    ?.noEntryGates || props?.reviewIdNameData?.dharamshala
                                        ?.entry_gate}</span>
                            </div>

                            <div className=''>
                                <p className={`${labelStyle}`}>No. of Exit Gates</p>
                                <span className={`${inputStyle}`}>{props?.reviewIdNameData?.dharamshala
                                    ?.noExitGate || props?.reviewIdNameData?.dharamshala
                                        ?.exit_gate}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>No. of Two Wheelers Parking Space</p>
                                <span className={`${inputStyle}`}>{props?.reviewIdNameData?.dharamshala
                                    ?.noTwoWheelerParking || props?.reviewIdNameData?.dharamshala
                                        ?.two_wheelers_parking}</span>
                            </div>
                            <div className=''>
                                <p className={`${labelStyle}`}>No. of Four Wheelers Parking Space</p>
                                <span className={`${inputStyle}`}>{props?.reviewIdNameData?.dharamshala
                                    ?.noFourWheelerParking || props?.reviewIdNameData?.dharamshala
                                        ?.four_wheelers_parking}</span>
                            </div>
                        </div>
                    </div>
                    {/* document */}
                    <div className='col-span-4 border border-dashed border-violet-800 '>
                        <h1 className='text-center p-3 mb-2 bg-white text-gray-600 font-sans font-semibold '>DOCUMENTS UPLOADED</h1>

                        <div className='grid grid-cols-1 md:grid-cols-1 lg:grid-cols-1 p-2 ml-12'>
                            {props?.reviewIdNameData?.dharamshalaDoc
                                ?.map((data, index) => (
                                    <div className=''>
                                        <>
                                            <p className={`${labelStyle}`}>{data?.docCode} Document</p>
                                            <div className='flex -mt-2'>
                                                <span className={`${inputStyle} flex-1`}>{data?.image?.name}</span>
                                                <span className={`mt-4  flex-1 float-right`}><img src="https://cdn-icons-png.flaticon.com/512/1829/1829589.png" alt="Preview Image" className={` w-8`} /></span>
                                            </div>
                                        </>
                                    </div>
                                ))}

                        </div>
                    </div>
                </div>

                <div className="grid grid-cols-12 w-full p-3">
                    <div className='md:pl-0 col-span-6'>
                        <button type="button" class="text-xs py-2 px-4 inline-block text-center mb-3 rounded leading-5 text-gray-100 bg-indigo-500 border border-indigo-500 hover:text-white hover:bg-indigo-600 hover:ring-0 hover:border-indigo-600 focus:bg-indigo-600 focus:border-indigo-600 focus:outline-none focus:ring-0" onClick={() => setFormIndex(2)}>back</button>
                    </div>
                    <div className='col-span-6'>
                        <button type="submit" class="float-right text-xs py-2 px-4 inline-block text-center mb-3 rounded leading-5 text-gray-100 bg-green-500 border border-green-500 hover:text-white hover:bg-green-600 hover:ring-0 hover:border-green-600 focus:bg-green-600 focus:border-green-600 focus:outline-none focus:ring-0" onClick={submitForm}>Submit</button>
                    </div>
                </div>
            </div>

         
        </>
    )
}

export default ReviewDharamshalaForm