import React from 'react'
import { Route, Routes } from 'react-router-dom'
import SelfAdvertisementIndexForm from './SelfAdvertisement/SelfAdvertisementIndexForm'
import MovableVehicleIndexForm from './MovableVehicle/MovableVehicleIndexForm'
import PrivateLandIndexForm from './PrivateLand/PrivateLandIndexForm'
import AgencyDetailIndexForm from './Agency/AgencyDetailIndexForm'
import AdvertisementDashboard from './AdvertisementDashboard'
import HoardingIndex from './Agency/Hoarding/HoardingIndex'
import SelfApprovalIndexForm from './SelfPrintable/SelfApprovalIndexForm'
import AgencyDashboard from './Agency/AgencyDashboard/AgencyDashboard'
import ViewAllHoardingPendingList from './Agency/Hoarding/ViewAllHoardingPendingList'
import ViewAllHoardingApprovedList from './Agency/Hoarding/ViewAllHoardingApprovedList'
import ViewAllHoardingRejectedList from './Agency/Hoarding/ViewAllHoardingRejectedList'
import ViewAllHoardingUnpaidList from './Agency/Hoarding/ViewAllHoardingUnpaidList'
import PaymentScreen from './PaymentScreen'
import BanquetMarriageHallFormIndex from '../marketing/MarketingModules/BanquetMarriageHall/BanquetMarriageHallFormIndex'
import DharamshalaIndex from '../marketing/MarketingModules/Dharmsala/DharamshalaIndex'
import HostelIndex from '../marketing/MarketingModules/Hostel/HostelIndex'
import LodgeIndex from '../marketing/MarketingModules/Lodge/LodgeIndex'
import MarketingDashboard from '../marketing/MarketingDashboard'
import HoardingRenewList from './Agency/AgencyDashboard/HoardingRenewList'
import HoardingArchivedList from './Agency/AgencyDashboard/HoardingArchivedList'
import HoardingBlacklistedList from './Agency/AgencyDashboard/HoardingBlacklistedList'
import AdvtPaymentReceiptIndex from './AdvtPaymentReceiptIndex'



function AdvertisementRoutes() {
    return (
        <>
            <div className=''>
                <Routes>
                    {/*//////////// Advertisement routes/////////////// */}
                    {/* <Route path="/advertDashboard" element={<AdvertisementDashboard />} /> */}
                    <Route path="/selfAdvrt/:id/:type" element={<SelfAdvertisementIndexForm />} />
                    <Route path="/movableVehicle/:id/:type" element={<MovableVehicleIndexForm />} />
                    <Route path="/privateLand/:id/:type" element={<PrivateLandIndexForm />} />
                    <Route path="/agency/:id/:type" element={<AgencyDetailIndexForm />} />
                    <Route path="/agencyDashboard/:id" element={<AgencyDashboard />} />
                    <Route path="/hoarding/:ulbId/:applicationId/:type" element={<HoardingIndex />} />
                    {/* <Route path="/hoarding" element={<HoardingIndex />} /> */}
                    <Route path="/paymentScreen/:paymentid/:wrkflowId" element={<PaymentScreen />} />
                    <Route path="/advtPaymentReceipt/:paymentId/:wrkflowId" element={<AdvtPaymentReceiptIndex/>} />
                    <Route path="/approvalLetter/:id/:workflowId" element={<SelfApprovalIndexForm />} />
                    {/* <Route path="/paymentSuccess" element={<PaymentSuccessScreen />} /> */}
                    <Route path="/hoarding-pending-list" element={<ViewAllHoardingPendingList />} />
                    <Route path="/hoarding-approved-list" element={<ViewAllHoardingApprovedList />} />
                    <Route path="/hoarding-Rejected-list" element={<ViewAllHoardingRejectedList />} />
                    <Route path="/hoarding-Unpaid-list" element={<ViewAllHoardingUnpaidList />} />
                    <Route path="/hoarding-Renew-list/:id" element={<HoardingRenewList />} />
                    <Route path="/hoarding-archived-list" element={<HoardingArchivedList />} />
                    <Route path="/hoarding-blacklisted-list" element={<HoardingBlacklistedList />} />

                    <Route path="/marketingDashboard" element={<MarketingDashboard />} />


                    {/*//////////// Marketing routes/////////////// */}
                    <Route path="/marriage-hall/:id/:type" element={<BanquetMarriageHallFormIndex />} />
                    <Route path="/dharamshala/:id/:type" element={<DharamshalaIndex />} />
                    <Route path="/hostel/:id/:type" element={<HostelIndex />} />
                    <Route path="/lodge/:id/:type" element={<LodgeIndex />} />


                </Routes>
            </div>
        </>
    )
}

export default AdvertisementRoutes