import React, { useEffect, useState } from 'react'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import axios from 'axios'
import AdvertisementApiList from '../../../AdvertisementApiList';
import ApiHeaderAdvrt from '../../../ApiHeaderAdvrt';
import Loader from '../../../Advertisement/Loader';
import BackButton from '../../../Advertisement/BackButton';
import AdvrtSuccessScreen from '../../../Advertisement/AdvrtSuccessScreen';
import HostelForm from './HostelForm';
import HostelDocForm from './HostelDocForm';
import ReviewHostel from './ReviewHostel';
import { useParams } from 'react-router-dom';
import BrandLoader from '../../../../../Components/Common/BrandLoader';
import CommonModal from '../../../../../Components/GlobalData/CommonModal';
import ServerErrorCard from '../../../ServerErrorCard';


function HostelIndex() {
    const [formIndex, setFormIndex] = useState(1) //formindex specifies type of form  at index 1 ...
    const [allFormData, setAllFormData] = useState([])
    const [responseScreen, setresponseScreen] = useState()
    const [reviewData, setreviewData] = useState({})
    const [show, setshow] = useState(false)
    const [existingApplicationDetails, setexistingApplicationDetails] = useState()
    const [applicationType, setapplicationType] = useState()
    const [isLoading, setisLoading] = useState(false);
    const [erroState, seterroState] = useState(false);

    const { id, type } = useParams();
    console.log("self advertisement param", id)
    console.log("self advertisement param", type)


    const showLoader = (val) => {
        setshow(val);
    }

    const { api_postHostelApplication, api_renewHostel_license, api_getHostelApplicationDetailsToRenew } = AdvertisementApiList()
    const notify = (toastData, type) => {
        toast.dismiss();
        if (type == 'success') {
            toast.success(toastData)
        }
        if (type == 'error') {
            toast.error(toastData)
        }
    };

    ///////////{*** COLLECTING ALL FORM DATA***}/////////
    const collectAllFormData = (key, formData, reviewIdName) => {
        console.log('prev of all Data', allFormData)
        console.log("review name by id in index...", reviewIdName)
        setAllFormData({ ...allFormData, [key]: formData })

        if (key == 'hostelForm') {
            console.log("data collecting by key", key, 'formData', formData, 'reviewData', reviewIdName)
            setreviewData({ ...reviewData, [key]: reviewIdName })
        }
        else {
            console.log('data not in review ===', key, '===', formData, 'preview...', reviewIdName)
            setreviewData({ ...reviewData, [key]: formData })
        }
    }
    console.log("all form data in index", allFormData)
    console.log("all form data in index for doc", allFormData?.selfAdvertisementDoc?.[0])


    ///// SUBMIT FORM /////
    const submitButtonToggle = () => {
        console.log('final form ready to submit...', allFormData)
        submitHostelForm()
    }

    const submitHostelForm = () => {
        setisLoading(true)

        let url
        let requestBody
        if (type == 'newapply') {
            url = api_postHostelApplication  //new apply
            requestBody = {
                // ulbId: allFormData?.selfAdvertisement?.ulb,
                ulbId: 2,
                rule: allFormData?.hostelForm?.rule,
                applicantName: allFormData?.hostelForm?.applicantName,
                fatherName: allFormData?.hostelForm?.fatherName,
                mobile: allFormData?.hostelForm?.mobileNo,
                email: allFormData?.hostelForm?.email,
                residentialAddress: allFormData?.hostelForm?.residenceAddress,
                residentialWardId: allFormData?.hostelForm?.residenceWardNo,
                permanentAddress: allFormData?.hostelForm?.permanentAddress,
                permanentWardId: allFormData?.hostelForm?.permanentWardNo,
                licenseYear: allFormData?.hostelForm?.licenseYear,
                entityName: allFormData?.hostelForm?.entityName,
                entityAddress: allFormData?.hostelForm?.entityAddress,
                entityWardId: allFormData?.hostelForm?.entityWardNo,
                holdingNo: allFormData?.hostelForm?.holdingNo,
                tradeLicenseNo: allFormData?.hostelForm?.tradeLicenseNo,
                longitude: allFormData?.hostelForm?.longitude,
                latitude: allFormData?.hostelForm?.latitude,
                organizationType: allFormData?.hostelForm?.organizationType,
                landDeedType: allFormData?.hostelForm?.landDeedType,
                waterSupplyType: allFormData?.hostelForm?.waterSupplyType,
                electricityType: allFormData?.hostelForm?.electricityType,
                securityType: allFormData?.hostelForm?.securityType,
                hostelType: allFormData?.hostelForm?.hostelType,
                messType: allFormData?.hostelForm?.messType,
                cctvCamera: allFormData?.hostelForm?.noOfCctv,
                noOfBeds: allFormData?.hostelForm?.noOfBeds,
                noOfRooms: allFormData?.hostelForm?.noOfRooms,
                fireExtinguisher: allFormData?.hostelForm?.noFireExtinguishers,
                entryGate: allFormData?.hostelForm?.noEntryGates,
                exitGate: allFormData?.hostelForm?.noExitGate,
                twoWheelersParking: allFormData?.hostelForm?.noTwoWheelerParking,
                fourWheelersParking: allFormData?.hostelForm?.noFourWheelerParking,
                aadharCard: allFormData?.hostelForm?.aadharNo,
                panCard: allFormData?.hostelForm?.panNo,
                isApproveByGovt: allFormData?.hostelForm?.isGovtInstitute,

                documents: allFormData?.hostelDoc
            }
        }
        if (type == 'renew') {
            url = api_renewHostel_license;//"renew url";
            requestBody = {
                applicationId: id,
                // ulbId: allFormData?.selfAdvertisement?.ulb,
                ulbId: 2,
                rule: allFormData?.hostelForm?.rule,
                applicantName: allFormData?.hostelForm?.applicantName,
                fatherName: allFormData?.hostelForm?.fatherName,
                mobile: allFormData?.hostelForm?.mobileNo,
                email: allFormData?.hostelForm?.email,
                residentialAddress: allFormData?.hostelForm?.residenceAddress,
                residentialWardId: allFormData?.hostelForm?.residenceWardNo,
                permanentAddress: allFormData?.hostelForm?.permanentAddress,
                permanentWardId: allFormData?.hostelForm?.permanentWardNo,
                licenseYear: allFormData?.hostelForm?.licenseYear,
                entityName: allFormData?.hostelForm?.entityName,
                entityAddress: allFormData?.hostelForm?.entityAddress,
                entityWardId: allFormData?.hostelForm?.entityWardNo,
                holdingNo: allFormData?.hostelForm?.holdingNo,
                tradeLicenseNo: allFormData?.hostelForm?.tradeLicenseNo,
                longitude: allFormData?.hostelForm?.longitude,
                latitude: allFormData?.hostelForm?.latitude,
                organizationType: allFormData?.hostelForm?.organizationType,
                landDeedType: allFormData?.hostelForm?.landDeedType,
                waterSupplyType: allFormData?.hostelForm?.waterSupplyType,
                electricityType: allFormData?.hostelForm?.electricityType,
                securityType: allFormData?.hostelForm?.securityType,
                hostelType: allFormData?.hostelForm?.hostelType,
                messType: allFormData?.hostelForm?.messType,
                cctvCamera: allFormData?.hostelForm?.noOfCctv,
                noOfBeds: allFormData?.hostelForm?.noOfBeds,
                noOfRooms: allFormData?.hostelForm?.noOfRooms,
                fireExtinguisher: allFormData?.hostelForm?.noFireExtinguishers,
                entryGate: allFormData?.hostelForm?.noEntryGates,
                exitGate: allFormData?.hostelForm?.noExitGate,
                twoWheelersParking: allFormData?.hostelForm?.noTwoWheelerParking,
                fourWheelersParking: allFormData?.hostelForm?.noFourWheelerParking,
                aadharCard: allFormData?.hostelForm?.aadharNo,
                panCard: allFormData?.hostelForm?.panNo,
                isApproveByGovt: allFormData?.hostelForm?.isGovtInstitute,

                documents: allFormData?.hostelDoc

            }
        }

        console.log('request body...', requestBody)
        axios.post(`${url}`, requestBody, ApiHeaderAdvrt())
            .then(function (response) {
                console.log('response after data submitted', response.data.data)
                setresponseScreen(response.data.data)
                setisLoading(false)
                notify("submitted successfully", "success")
            })
            .catch(function (error) {
                console.log('errorrr.... ', error);
                setisLoading(false)
                notify(error.response.data.message`${"failed to submit"}`, "error")

            })
    }

    // passing values in components
    const values = {
        setFormIndex: setFormIndex,
        showLoader: showLoader,
        collectFormDataFun: collectAllFormData,
        toastFun: notify,
        type: type,
        existingApplicationDetails: existingApplicationDetails,
    }

    useEffect(() => {
        setapplicationType(type)
        if (type == 'renew') {
            fetchApplicationDetails()
        }
    }, [])
    const fetchApplicationDetails = () => {
        seterroState(false)
        setisLoading(true)
        let requestBody = {
            applicationId: id,
        }

        console.log('renew application id', requestBody)
        axios.post(`${api_getHostelApplicationDetailsToRenew}`, requestBody, ApiHeaderAdvrt())
            .then(function (response) {
                console.log('dharamshala application detail for renew application.....', response)
                setexistingApplicationDetails(response.data.data)
                setisLoading(false)
            })
            .catch(function (error) {
                console.log('==2 details by id error...', error)
                seterroState(true)
                setisLoading(false)
            })
    }

    console.log("response screen", responseScreen)
    if (responseScreen?.status == true) {
        return (
            <>
                <AdvrtSuccessScreen responseScreenData={responseScreen} />
            </>
        )
    }

    if (isLoading) {
        return (
            <>
                <BrandLoader />
            </>
        )
    }
    if (erroState) {
        return (
            <CommonModal>
                <ServerErrorCard title="Server is busy" desc="Server is too busy to respond. Please try again later." buttonText="View Dashboard" buttonUrl="/propertyDashboard" />
            </CommonModal>
        )
    }

    return (
        <>

            <ToastContainer position="top-right"
                autoClose={2000} />

            <div className='overflow-x-clip  p-4 mt-3 container mx-auto h-screen'>
                <div className='grid grid-cols-1 md:grid-cols-2 lg:grid-cols-2 bg-white p-2 rounded-md shadow-md shadow-violet-200  '>
                    <div className=''>
                        <div className='flex flex-row '>
                            <h1 className='text-2xl ml-4 text-gray-600 font-sans font-semibold'>HOSTEL HALL REGISTRATION APPLICATION</h1>
                        </div>
                        <h1 className='text-xs ml-3 p-1 text-gray-600 font-sans'>
                            You Can Get License To Advertise Your Business Name On Your Shop
                        </h1>
                    </div>
                    <div>
                        <div className='flex flex-row mt-2 float-right'>
                            <span className='text-md font-bold md:text-xl text-violet-600 text-center  transition-all animate-wiggle'>&emsp; <strong className='text-2xl text-violet-600 '>{3 - formIndex}
                            </strong> more screen</span>
                            <img src='https://cdn-icons-png.flaticon.com/512/2376/2376320.png' className='h-10 mr-4  opacity-80 float-right ml-4' />
                        </div>
                    </div>
                </div>
                <div className={`${formIndex == 1 ? 'translate-x-0' : 'translate-x-full'} transition-all`}><HostelForm values={values} /></div>
                <div className={`${formIndex == 2 ? 'translate-x-0' : 'translate-x-full'} transition-all`}><HostelDocForm values={values} /></div>
                <div className={`${formIndex == 3 ? 'translate-x-0' : 'translate-x-full'} transition-all`}><ReviewHostel values={values} reviewIdNameData={reviewData} allFormData={allFormData} submitFun={submitButtonToggle} /></div>
            </div>
        </>
    )
}

export default HostelIndex